ul, li {
  list-style: none;
}

h1 {
  text-align: center;
  font-weight: 400;
  color: grey;
  margin-bottom: 30px;
}

li:last-child {
  border-bottom: none;
}

a:focus,
a:active,
a:hover,
button:focus,
button:active,
button:hover {
  outline: none;
  box-shadow: none !important;
}

.navbar-nav .dropdown-menu {
  position: absolute;
}

.btn.disabled, .btn:disabled {
  cursor: not-allowed;
}

@media (min-width: 900px) {
  .navbar-collapse.hidden-toggle-nav {
    display: none !important;
  }
}
.movie-bgc {
    position: absolute;
    opacity: 0.4;
    top: 0;
    left: 0;
    width: 100%;
    height: 120%;
    background-size: cover;
}

.movie-bgc:after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 120%;
    background-image: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 1) 100%);
    background-image: linear-gradient(to top, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 1) 100%);
}
